body {
  margin: 0;
  padding: 0;
}

nav {
  display: flex;
  justify-content: center;
  width: 100%;
  border-top: 0.2px solid #bfbaba;
  border-bottom: 0.2px solid #bfbaba;
  align-items: center;
  height: 50px;
}

.nav-menu {
  border-bottom: 0.2px solid #bfbaba;
  display: flex;
  justify-content: space-evenly;
  padding-bottom: 50px;
  padding-top: 50px;
  position: absolute;
  z-index: 1;
  background-color: white;
  width: 100%;
  }

.nav-menu-items {
  width: 90%;
  font-family: 'Rajdhani', sans-serif;
  text-align:left;
  display: flex;
  flex-direction: column;
  margin-left: 90px;
}

.nav-menu-items p {
  margin: 10px 0;
}

.nav-p-items {
  font-family: 'Fjalla One', sans-serif;
}

.has-left-border {
  border-left: 0.2px solid #bfbaba;
}

.mainNav {
  font-family: 'Fjalla One', sans-serif;
  display: flex;
  justify-content: center;
  list-style: none;
  width: 70%;
  margin: 0;
  height: 100%;
  padding: 0;
}

li {
  margin-bottom: 0px;
  cursor: pointer;
  padding: 14px;
}
 li:hover {
  border-bottom: 1px solid black;
 }

 .search {
  border: 1.2px solid #bfbaba;
  height:70%;
 }

 .searchInput {
  font-family: sans-serif;
  border: none;
  padding-top: 4%;
  padding-left: 19%;
  padding-right: 10%;
  padding-bottom: 2%;
  height: inherit;
 }

 .searchInput:focus { 
  outline: none;
 }

 .icon h2 {
  margin: 0;
  height: 100%;
  font-family: 'Fjalla One', sans-serif;
 }

 @media only screen and (max-width: 1200px) {
  nav {
    flex-direction: column;
    height: auto;
    border-bottom: 0.2px solid #000000;
  }
  .mainNav {
    flex-direction: column;
  }
  .search {
    border: 1px solid black;
    height: 100%;
    margin-bottom: 14px;
   }

   li:hover {
    border-bottom: none;
   }
}

.container{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
  margin-left: 25px;
  margin-right: 25px;
}


.section1 {
  display: flex;
  flex-direction: column;
  max-width: 100%;
  justify-content: space-around;
  margin-bottom: 15px;
}


.featuredSection {
  background-color: black;
  background-size: cover;
  background-position: center;
  height: 500px;
  max-width: 100%;
}

.shopAll {
  display: flex;
  align-items: flex-end;
  padding: 50px
}

.shopNote {
  text-align: initial;
  position: absolute;
  display: flex;
  flex-direction: column;
  width: 100%;
  padding-left: 25px;
  padding-bottom: 25px;
}
.shopNote h1 {
  font-family: 'Karla', sans-serif;
  font-size: 70px;
  color: white;
}

.shopNote a {
  text-decoration: none;
  background-color: white;
  padding: 18px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  border-radius: 57px;
  font-family: 'Karla', sans-serif;
  color: black;
}

.featuredFigure {
  margin: 0;
  height: inherit;
  position: relative;
  display: flex;
  align-items: flex-end;
}

.featuredImage {
  object-fit: cover;
  width: 100%;
  height: inherit;
}

.imageShow {
  margin: 15px auto;
  height: inherit;
  position: relative;
  display: flex;
  flex-direction: column;
}

.imageShow img {
  max-width: 100%;
}

.itemInfo {
  display: flex;
  justify-content: space-between;
  font-family: 'Karla', sans-serif;
  color: black;
  max-width: 90%;
}

.Info {
  display: flex;
  justify-content: space-between;
  font-family: 'Karla', sans-serif;
  color: black;
  max-width: 100%;
}

.black {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  /* gap: 20px; */
}

.MuiSvgIcon-root {
  fill: black !important;
  font-size: 2rem !important;
}

.slick-slider {
  margin: 30px
}

.slick-slide {
  padding: 40px
}

@media only screen and (max-width: 500px) {
  .shopNote h1 {
    font-size: 40px;
  }
}

@media only screen and (min-width: 1024px) {
  .featuredSection {
    height: 700px;
  }
}

.MuiSvgIcon-root {
  fill: #6f7880 !important;
  font-size: 2rem !important;
}

.slick-slider {
  margin: 0 14px;
}

.slick-slide {
  padding: 20px;
}

.itemInfo {
  display: flex;
  justify-content: space-between;
  font-family: 'Karla', sans-serif;
  color: black;
  max-width: 90%;
  font-family: 'Fjalla One', sans-serif;
}
.App {
  text-align: center;
}

